import React from "react"
import addToMailchimp from "gatsby-plugin-mailchimp"

export default class MailList extends React.Component {
  state = {
    name: null,
    email: null,
  }

  _handleChange = e => {
    console.log({
      [`${e.target.name}`]: e.target.value,
    })
    this.setState({
      [`${e.target.name}`]: e.target.value,
    })
  }

  _handleSubmit = e => {
    e.preventDefault()

    console.log("submit", this.state)

    addToMailchimp(this.state.email, this.state)
      .then(({ msg, result }) => {
        console.log("msg", `${result}: ${msg}`)

        if (result !== "success") {
          throw msg
        }
        alert(msg)
      })
      .catch(err => {
        console.log("err", err)
        alert(err)
      })
  }

  render() {
    return (
      <div class="text-center">
        <h4 class="text-success">سجل معنا ليصلك كل جديد</h4>
        <b class="text-success">احصل على أحدث المواضيع</b>
        <div>
          <form dir="rtl" class="form-group" onSubmit={this._handleSubmit}>
            <input
              class="form-control"
              type="text"
              onChange={this._handleChange}
              placeholder="إسـمك"
              name="name"
            />
            <br />
            <input
              class="form-control"
              type="email"
              onChange={this._handleChange}
              placeholder="بريدك الإليكتروني"
              name="email"
            />
            <br />

            <button type="submit" class="btn btn-success btn-sm">
              <i class="far fa-paper-plane"></i> سجــل
            </button>
          </form>
        </div>
      </div>
    )
  }
}
